const mutations={
    mutationsIsShowMoreMenus:function(state,payload){
        //state表示当前状态数据
        //payload表示使用方传递的附加数据
        state.isShowMoreMenus=payload;
    },
    //实现用户登录
    mutationsLogin:function(state){
        state.login=true;
    },
    //修改loginname
    mutationsLoginname:function(state,payload){
        state.loginname=payload;
    },
    mutationsAccesstoken:function(state,payload){
        state.accesstoken=payload;
    },
    //注销
    mutationsLoginout:function(state){
        state.login=false;
        state.accesstoken='';
        state.loginname='';
    }
}
export default mutations;


