export default {
    name:'Loginout',
    created() {
        //注销
        //1.恢复VUEX中的数据为原始数据
        this.$store.commit('mutationsLoginout');
        //2.清空本地存储
        window.localStorage.clear();
        //3.页面跳转
        this.$router.push('/Topiclist/all');

    },
}



