import Vue from 'vue'
import Router from 'vue-router'
import VueRouter from 'vue-router'

import Home from '@/components/Home.vue'
// import Hello from '@/components/HelloWorld.vue'
import Topiclist from '@/components/Topiclist.vue'
import Topic from '@/components/Topic.vue'
import Login from '@/components/Login.vue'
import Loginout from '@/components/Loginout.vue'
import Center from '@/components/Center.vue'
import Msg from '@/components/Msg.vue'
import User from '@/components/User.vue'
//全局注册路由
Vue.use(Router)

//定义路由信息对象

const routes= [
    // {
    //   path: '/',
    //   name:'Home',
    //   component: Home
    // },
   
    {
        path: '/',
        redirect:'/Topiclist/all' ,// 重定向（访问根路径是，默认显示指定路由）
        props:true 
      
      },
    {
      path: '/Topiclist/:name',
      name:'Topiclist',
      component: Topiclist,
      props:true//然后在模块页面props:["name"]，就可以直接获取动态参数传递给路由组件
    },
    {
      path: '/Topic/:id',
      name:'Topic',
      component: Topic,
      props:true//然后在模块页面props:["id"]，就可以直接获取动态参数传递给路由组件
    },
    {
      path: '/Login',
      name:'Login',
      component: Login,
  
    },
    {
      path: '/Loginout',
      name:'Loginout',
      component: Loginout,
  
    },
    //用户中心
    {
      path: '/Center',
      name:'Center',
      component: Center,
    },
    {
      path: '/User/:loginname',
      name:'User',
      component: User,
      props:true
    },
    {
      path: '/Msg',
      name:'Msg',
      component: Msg,
  
    },
  ];

  // 创建路由管理器

  const router=new VueRouter({
    routes
  });

  //对外输出模块
  export default router;


