import Vue from 'vue';
import Vuex from 'vuex';
import mutations from "./mutations.js";
import actions from "./actions.js";
//全局注册状态管理器
Vue.use(Vuex)

//创建store对象
const store=new Vuex.Store({
    //状态数据(四个)
    state:{
        //主体分类数据
        Topic:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'}
          ],
        //   未登录时的更多菜单
          UnLoginedinMoreMenus:
            {login:"登录"}
          ,
          //   登录时的更多菜单
          LoginedinMoreMenus:
            {
              center:"个人主页",
              msg:"消息中心",
              loginout:"注销登录"
            }
          ,
          //   是否是登录状态
          login:false,
          loginname:'',
          accesstoken:'',
          //   actionsheet是否显示菜单
          isShowMoreMenus:false
        },
    
    //状态数据的计算属性
    getters:{
        MoreMenus:function(state){
            if(state.login){
              // 用户处于登录状态，返回登陆后的更多菜单
              return state.LoginedinMoreMenus;
            }
              return state.UnLoginedinMoreMenus;
            }
    },
    mutations,
    actions
});


  //对外输出模块
  export default store;


